import { Navigate, Outlet } from "react-router-dom"
import DasRightSectionPage from "../admin/daseBoardRightSection/DasBoardRightSection"


function PrivateRoute({ component: component, tokenNoti, walletData,services ,setProfileData ,isInstantpaypresnt}) {
    const token = window.localStorage.getItem('userIdToken')
    return token ? <DasRightSectionPage tokenNoti={tokenNoti} walletData={walletData} PrivateRoute={PrivateRoute} services={services} setProfileData ={setProfileData} isInstantpaypresnt={isInstantpaypresnt}/> : <Navigate to='/login-area' />
}
export default PrivateRoute